.registerStyle{
  display: flex;
  flex-direction: row;
  justify-content: center; 
  color: white;
  border: 1px solid white;
  padding: 50px;
  }

.rightPanel {
  padding: 40px;
  margin: 10px;
  float: right;
  width: 100%;
  height: 100%;
  overflow:auto;
  background-color: #867B7B;
}

.RightContent{
  background-color: #867B7B;
  width: 100%;
  font-size: 0.8rem;
  color: #ffffff;
  max-width: 100%;
  max-height: 100%;
}
.col-form-label {
  text-align: right;
  color: black;
}

@media only screen and (max-width: 768px) {
  .col-form-label {
    text-align: left;
    color: white;
  }
  .rightPanel {
    width: 100%;
    margin-bottom: 50px;
  }
}

.buttonsContainer {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}
